.button {
    padding: 0 10px;
    width: auto;
    height: 50px;
    border-radius: 50%;
    border: 2px solid #000;
}

.page-button {
    background-color: transparent;
    padding: 0 10px;
    width: auto;
    height: 50px;
    border-radius: 10px;
    border: 2px solid #000;
}

.character-name,
.character-id {
    max-width: 70%;
    font-weight: bold;
    font-size: 25px;
}

.my-badge {
    width: 80%;
    font-size: 20px;
    margin: 20px auto 0;
    font-family: 'Special Elite', cursive;
    font-size: 25px;
    font-weight: bold;
    text-align: center;
    text-shadow: 2px 0 0 #000, -2px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 1px 1px #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000;
    color: #00b0c7;
}

.prev,
.next {
    width: 25px;
    height: 25px;
}

.next {
    transform: scaleX(-1);
}

.character-img {
    width: 80%;
    margin: 0 auto;
    border: 3px solid #000;
    border-radius: 50%;
    transform: scale(1);
    animation-name: example;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

.principal-info {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 10px 0;
}

ion-icon {
    background-color: #fff;
    border-radius: 50%;
    width: 30px;
    height: 30px;
}

.buttons-container {
    display: flex;
    justify-content: space-around;
    width: 80%;
    margin: 20px auto;
    align-items: center;
}

.buttons-container span {
    font-family: 'Sedgwick Ave', cursive;
    letter-spacing: 3px;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    text-shadow: 2px 0 0 #000, -2px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 1px 1px #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000;
    color: #00b0c7;
}

@keyframes example {
    from {
        transform: scale(1);
    }
    to {
        transform: scale(1.1);
    }
}